                                </v-layout>

                                <!--Seccao dos dados pessoais-->
                                <v-layout id="dadosPessoais" class="elevation-2" style="height: 450px">

                                    <explicador-personal-info :user="user"></explicador-personal-info>

                                </v-layout>

                                <v-layout v-if="isMyProfile" id="fotoDePerfil" class="elevation-2" style="height: 450px">

                                    <explicador-photo-info :user="user"></explicador-photo-info>

                                </v-layout>

                                <!--Sessao de Dados Academicos-->
                                <v-layout id="dadosAcademicos" class="elevation-2" style="height: 450px">

                                    <explicador-academic-info :user="user"></explicador-academic-info>

                                </v-layout>

                                <!--Sessao de Dados de Localizacao-->
                                <v-layout id="localizacao" class="elevation-2" style="height: 450px">

                                    <explicador-location-info :user="user"></explicador-location-info>

                                </v-layout>

                                <v-layout v-if="!isMyProfile" id="contact" class="elevation-2" style="height: 450px">

                                    <explicador-contact-info :user="user"></explicador-contact-info>

                                </v-layout>


                                <!--Seccao do inquerito-->
                                <v-layout v-if="isMyProfile && userVerified" id="email-verify" class="mt-2 mb- elevation-2">

                                    <v-flex xs12>
                                        <v-expansion-panel>
                                            <v-expansion-panel-content expand-icon="infos">

                                                <div slot="header">
                                                    <span style="right: 5px;  margin-left: 10px; font-size: 18pt; color: darkblue">
                                                        Voçê ainda não verificou o seu email!
                                                    </span>
                                                    <br>
                                                    <v-btn
                                                        small=""
                                                        round=""
                                                        flat=""
                                                        color="green"
                                                        title="Clique para te enviarmos o link de verificacao"
                                                        @click.stop="sendVerificationLink"
                                                    >
                                                        Enviar Link de verificacao

                                                        <v-icon>mail</v-icon>
                                                    </v-btn>
                                                    <!--<div class="mr-"></div>-->
                                                    <v-btn
                                                            small=""
                                                            flat=""
                                                            color="red"
                                                    >
                                                        <v-icon>info</v-icon>
                                                        Saiba mais
                                                    </v-btn>
                                                </div>
                                                <v-card>
                                                    <v-card-text>
                                                        <h3>Porque confirmar os dados?</h3>
                                                        <br>
                                                        <br>
                                                        <p>Utilizadores com dados não confirmados:</p>
                                                        <ul class="ml-5">
                                                            <li>
                                                                <p>Não serão confiados</p>
                                                            </li>
                                                            <li>
                                                                <p>Poderao não ser informados sobre muitas novidades</p>
                                                            </li>
                                                            <li>
                                                                <p>Serão ocultados ou serão os ultimos a ser alistados</p>
                                                            </li>
                                                        </ul>
                                                        <br>
                                                        <p  class="pa-2" style="border-left: 2px solid blueviolet; background: rgba(200,200,200,.2)">A confirmação de dados é importante, porque dá certeza as pessoas que te procuram de que os seus dados de contactos são válidos.</p>
                                                        <br>
                                                        <h3>Que dados confirmar?</h3>
                                                        <br>
                                                        <p>Por enquanto confirme apenas o seu email! é bom que faças isso urgente para não perder nada.</p>
                                                        <b>Confirme o seu email, clique no botao acima para te enviamos para o seu email o link de verificacao!</b>

                                                        <br>
                                                        <br>
                                                        <p class="pa-2" style="border-left: 2px solid blueviolet; background: rgba(200,200,200,.1)">Pode <b @click.prevent="sendVerificationLink">Clicar aqui</b>,
                                                            vai para o seu email, clicar no link longo que vamos te enviar, esse e o link de verificacao
                                                        </p>
                                                    </v-card-text>
                                                </v-card>
                                            </v-expansion-panel-content>
                                        </v-expansion-panel>
                                    </v-flex>

                                </v-layout>

                            </v-flex>
                        </v-layout>
                    </v-container>
                </v-flex>

            </v-layout>


        </v-container>

    </div>

</template>

<script>
    export default {
        props: ['user', 'name'],
        data: () => ({
            hidden: false,
            nameU: '',
            isThis: false,
        }),
        created () {

            if (this.isConfirmMailSent)
            {
                setTimeout(this.mostrarDialog(), 150000);

            }

        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint',
                    controlRoute: false,
                }
            },
            isUser () {
                return this.user !== undefined && this.user !== null
            },
            img () {
                return this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            userVerified () {
                return !this.user.emailVerified
            },
            gender () {

                const negative = 'Sexo nao definido'

                if (!this.isUser) return negative

                let genero = this.user.gender

                return genero && genero !== null && genero && genero !== '' ? genero : negative


            },
            userInstitution () {

                if (!this.isUser) return 'Sem Universidade (Escola)'

                let inst = this.user.Educations.institution
                return inst !== null && inst !== '' ? inst : 'Sem Universidade (Escola)'
            },
            userFaculty () {

                const negative = 'Sem Faculdade'

                if (!this.isUser) return negative

                let fac = this.user.Educations.faculty
                return this.isUser && fac !== null && fac !== undefined && fac !== '' ? 'Faculdade de ' + fac : negative
            },
            userCourse () {

                const negative = 'Sem Curso'

                if (!this.isUser) return negative

                let course = this.user.Educations.course

                return course && course !== null && course !== undefined && course !== '' ? 'Curso de ' + course : negative

            },
            userDepartment () {

                const negative = 'Sem Departamento'

                if (!this.isUser) return negative

                let department = this.user.Educations.department

                return department && department !== null && department !== undefined && department !== '' ? department : negative


            },
            userNivel () {

                const negative = 'Sem Nivel'

                if (!this.isUser) return negative

                let nivel = this.user.Educations.nivel

                return nivel && nivel !== null && nivel !== undefined && nivel !== '' ? nivel : negative


            },
            hasSubjects () {

                if ( !this.isUser ) return false

                let subjects = this.user.Educations.subjects

                return subjects.length > 0
            },

            subjectsTitle () {

                if (!this.hasSubjects) return 'Ainda não registou as disciplinas que explica'

                return 'Disciplinas do ' + this.nomeUser
            },
            userSubjects () {

                const negative = []

                if (!this.isUser) return negative

                let subjects = this.user.Educations.subjects

                return subjects


            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Location

            },

            userProvince () {

                const negative = 'Provincia Não registada'

                if (!this.hasSomeLocation) return negative

                let userProv = this.user.Location.province

                return userProv !== null && userProv !== undefined && userProv !== '' ? userProv : negative

            },

            userCity () {

                const negative = 'Cidade Não registada'

                if (!this.hasSomeLocation) return negative

                let userCity = this.user.Location.city

                return userCity !== null && userCity !== undefined && userCity !== '' ? userCity : negative

            },

            userBairro () {

                const negative = 'Bairro Não registado'

                if (!this.hasSomeLocation) return negative

                let userBairro = this.user.Location.bairro

                return userBairro !== null && userBairro !== undefined && userBairro !== '' ? userBairro : negative

            },

            route() {
                return this.$route.path
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },

            isConfirmMailSent () {
                return this.$store.getters.getConfirmMailSent
            },

        },
        watch: {

            route(value) {

                window.location.reload(false);

            },
            loading (value) {
                if (!value && !this.error && this.isThis) {

                    this.mostrarDialog()
                    this.isThis = false

                }
            }

        },
        methods: {

            scrollTo (el) {

                if (isNaN(el))
                {
//                    Scroll to a certain element
                    document.querySelector(el).scrollIntoView({
                        behavior: 'smooth'
                    });
                }
                else
                {
                    window.scroll({
                        top: el,
                        left: 10,
                        behavior: 'smooth'
                    });
                }




            },
            sendVerificationLink () {
                this.isThis = true
                this.$store.dispatch('sendVerificationLink')
            },
            mostrarDialog () {

                const title = 'Email de confirmação enviado com sucesso .. .'
                const message = 'Acabamos de te enviar um email. Acesse a caixa de entrada na sua conta de email para confirmar. Te enviamos um link de confirmação, e só clicar. Cumprimentos.'
                const btn1 = ''
                const btn2 = 'Ok'

                /**
                 * Primeiro param: true: significa abrir a dialog
                 * Segundo param:  title: o titulo a mostrar
                 * Terceiro param:  msg: a mensagem a mostrar
                 */
                this.$store.dispatch('togleConfirmDialog', {show: true, title: title, message: message,btn1: btn1,btn2: btn2})
                this.$store.dispatch('confirmationMailSet', false) //mostra que o email de confirmacao ja foi enviado

            },

        },
    }
</script>
<style scoped>
    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }

    .elevation-10 {
        /*box-shadow: 0 6px 6px -3px rgba(0,0,0,.2),0 10px 14px 1px rgba(255,9,5,.6),0 4px 1px 3px rgba(0,0,0,.12)!important;*/
    }

</style>


