                   flat
                   round
                   color="red"
                   title="Ocultar "
                   slot="activator"
                   @click.stop="closeDrawer"
                   v-if="temporary"
               >
                   <v-icon>close</v-icon>
               </v-btn>
           </v-layout>
        </template>
        <v-list dense class="height">
            <template>

                <!--Select da disciplina encima no drawer esquerdo-->
                <v-layout justify-center fill-height v-if="isHomePage">
                    <v-flex xs11>
                        <v-select
                                v-bind:items="tipos"
                                v-model="e2"
                                label="Selecione o filtro"
                                prepend-icon="content_paste"
                                single-line
                                auto
                                hide-details
                        ></v-select>
                    </v-flex>
                </v-layout>

                <!--Campo de pesquisa encima no drawer esquerdo-->
                <v-layout v-if="isHomePage">
                    <v-flex>
                        <v-text-field
                                light
                                solo
                                v-model="searchedData"
                                prepend-icon="search"
                                :placeholder="'Pesquisar ' + comp_searchedData"
                                class="elevation-0"
                                style="max-width: 400px; min-width: 128px"
                        ></v-text-field>
                    </v-flex>
                </v-layout>

                <!--Todo: Primeiro divider que poe a linha de divisao-->
                <!--<v-divider v-if="isHomePage"></v-divider>-->

                <!--Opcao de configuracao que aparece em baixo da lista-->
                <template v-if="filtering !== ''">
                    <v-divider></v-divider>
                    <v-list-tile class="primary" :title="'Apenas explicadores de ' + filtering">

                        <v-list-tile-content class="ml-5">
                            <v-list-tile-title>
                                {{ filtering }}
                            </v-list-tile-title>
                        </v-list-tile-content>
                        <v-list-tile-action title="Apagar o filtro" @click="onCloseFilter" style="cursor: pointer">
                            <v-icon>close</v-icon>
                        </v-list-tile-action>
                    </v-list-tile>
                </template>

                <!--Todo: Layout com a lista das discipplinas que sao mostradas, falta colocar o scroll-->
                <div v-for="item in items">
                    <v-divider></v-divider>
                    <template>
                        <v-list-tile @click="onSelect(item)" :title="'Ver explicadores de ' + filtering">
                            <v-list-tile-action>
                                  <v-icon>library_books</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>
                                    {{ item }}
                                </v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>
                    </template>
                </div>

                <!--Notificacao que aparece quando nao ha resuultados de pesquisa.-->
                <div v-if="items.length === 0">
                    <template>
                        <v-list-tile @click.prevent="">
                            <v-list-tile-action>
                                <v-icon class="red--text">youtube_searched_for</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title class="red--text">
                                    Sem resultados . . .
                                </v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>
                    </template>
                </div>

                <!--Botao para o envio de sugestao, que aparece apenas para os users autenticados-->
                <div v-if="isUserAuth">
                   <sugestion-dialog></sugestion-dialog>
                </div>

            </template>

            <!--Todo: Segundo divider que poe a linha de divisao-->
            <v-divider></v-divider>
        </v-list>
    </div>
</template>
