<template>
    <v-container fluid grid-list-md>
        <!--<div>-->
            <!--<img width="80" style="background: #1565c0" height="80" src="https://user-images.githubusercontent.com/18400142/37566968-cfca5484-2ac8-11e8-89a2-3cec6e124af4.png" alt="Mindd"/>-->
        <!--</div>-->
        <v-layout wrap v-if="searchedUsers.length > 0">

            <template v-for="user in searchedUsers">
                <v-flex xs12 sm4 md3>
                    <explicador :user="user"></explicador>
                </v-flex>
            </template>

        </v-layout>

        <v-layout v-else>

            <v-flex v-if="searchedData === '' && filterType === '' && filterName === ''" xs12 class="text-xs-center blue--text">
                <h3>Processando ...</h3>
            </v-flex>

            <v-flex v-else="" xs12 class="text-xs-center red--text">
                <div style="height: 200px"></div>

                <h3 v-if="filterType === '' && filterName === ''"><v-icon>search</v-icon> Nenhum explicador encontrado ... </h3>

                <div v-else=''>
                    <h3
                       v-if="'DisciplinasCursosUniversidadesFaculdades'.includes(filterType)"
                    ><v-icon>search</v-icon>
                        Nenhum explicador "{{ searchedData }}" encontrado de ...
                    </h3>
                    <h3 v-else=""><v-icon>search</v-icon> Nenhum explicador "{{ searchedData }}" encontrado em ... </h3>
                    <h3>{{ filterType }} |  {{ filterName }} </h3>
                    <v-btn
                            round=""
                            flat=""
                            title="Clique aqui para enviar sugestão de registo ao explicador">
                        Conhece Algum explicador nessa área?
                    </v-btn>
                    <br>
                    <v-btn
                            round=""
                            flat=""
                            @click="onCloseFilter"
                    >
                        Voltar
                    </v-btn>
                </div>


            </v-flex>
        </v-layout>

        <explicador-help-dialog></explicador-help-dialog>
    </v-container>
</template>

<script>
    import { eventBus } from '../main'
    export default {
        beforeCreate () {
            this.$Progress.start()
        },
        mounted () {

            if (this.users.length > 0)
            {
                this.$Progress.finish()
            }

            eventBus.$on('searchForExplicador',  (nameOrUniversity) => {

                this.searchedData = nameOrUniversity

            })

            //Metodo chamado no MainMenuLeft.vue e SearchResult.vue
            //Recebe item: o que deve ser procurado ex: 'maxaque c'
            //Recebe filterType: O tipo de filtro ex: 'Bairro'
            eventBus.$on('filterExplicadoresBy',  (item, filterType) => {
                console.log('item recebido: ', item,' filterType recebido: ', filterType)
                this.filterType = filterType
                this.filterName = item

            })
            eventBus.$on('resetExplicadoresFilter', () => {

                this.filterType = ''
                this.filterName = ''

            })
        },
        data () {
            return {
                searchedData: '',
                filterType: '',
                filterName: ''
            }
        },
        computed: {
            users () {
                return this.$store.getters.getUsers
            },
            areUsers () {
                return this.users.length > 0
            },
            searchedUsers () {

                if (!this.areUsers)  return []

                if (this.filterType === '' && this.filterName === '') {

                    return this.users.filter(u => {
                        return this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                                || this.searchByEducationAndLocation(u, this.searchedData) //Pesquisa por quase tudo, veja o metodo
                    })

                }
                else
                {
                    if (this.filterType === '') return this.filterBySubject

                    return this.filterAdvanced
                }



            },
//          Dependendo da opcao que se clica, mostramos os explicadores correspondentes e eh nesse onde se fara pesquisa.
            filterAdvanced ()
            {
                return  this.filterType === 'Províncias' ? this.filterByProvince  :
                        this.filterType === 'Cidades' ? this.filterByCity  :
                        this.filterType === 'Distritos' ? this.filterByDistrict  :
                        this.filterType === 'Dist. Municipais' ? this.filterByMunicipalDistrict  :
                        this.filterType === 'Postos Administrativos' ? this.filterByPost  :
                        this.filterType === 'Bairros' ? this.filterByBairro  :
                        this.filterType === 'Disciplinas' ? this.filterBySubject  :
                        this.filterType === 'Cursos' ? this.filterByCourse :
                        this.filterType === 'Faculdades' ? this.filterByFaculty :
                        this.filterType === 'Universidades' ? this.filterByUniversity :
                        this.filterType === 'Departamentos' ? this.filterByDepartment : []

            },

            filterByProvince () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.province && u.Locations.province === this.filterName)
                })

            },

            filterByCity () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.city && u.Locations.city === this.filterName)
                })

            },

            filterByDistrict () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.district && u.Locations.district === this.filterName)
                })

            },

            filterByMunicipalDistrict () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.municipalDistrict && u.Locations.municipalDistrict === this.filterName)
                })

            },

            filterByPost () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.post && u.Locations.post === this.filterName)
                })

            },

            filterByBairro () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasLocation(u) && u.Locations.bairro && u.Locations.bairro === this.filterName)
                })

            },

            filterBySubject () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasEducation(u) && this.hasSubjects(u)
                        && u.Educations.subjects.find(s => {
                            return s.name === this.filterName
                        }))
                })
            },
            filterByCourse () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasEducation(u) && u.Educations.course === this.filterName)
                })

            },

            filterByFaculty () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasEducation(u) && u.Educations.faculty === this.filterName)
                })

            },
            filterByUniversity () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasEducation(u) && u.Educations.institution === this.filterName)
                })

            },
            filterByDepartment () {

                return this.users.filter(u => {
                    return (this.hasName(u) && u.name.toLowerCase().includes(this.searchedData.toLowerCase())
                        || this.searchByEducationAndLocation(u, this.searchedData)) //Pesquisa por quase tudo, veja o metodo
                        && (this.hasEducation(u) && u.Educations.department && u.Educations.department === this.filterName)
                })

            }
        },
        watch: {
            users (value) {
                this.$Progress.finish()
            },
            searchedUsers (value)
            {
                eventBus.$emit ('advancedSearchResult', value) //evento tratado no ficheiro App.vue
            },
        },
        methods: {
            onCloseFilter () {
                // Reseta os criterios de pesquisa de dados na pagina, para o default
                // Este evento eh tratado nos metodos created () dos Componentes Home.vue e MainMenuLeft.vue
                eventBus.$emit('resetExplicadoresFilter');
            },
            hasName (user)
            {
                return user.name !== undefined && user.name !== null
            },

            hasEducation (user) {
                const userEducation = user.Educations
                return userEducation && userEducation !== null
            },

            hasLocation (user) {
                const userLocation = user.Locations
                return userLocation && userLocation !== null
            },

            // Chamando os demais metodos que fazem a pesquisa pelos dados educacionais
            searchByEducationAndLocation (u, searchedData)
            {
                return this.hasInstitution(u, searchedData)
                || this.hasSubject(u, searchedData)
                || this.hasFaculty(u, searchedData)
                || this.hasDepartment(u, searchedData)
                || this.hasCourse(u, searchedData)

                || this.searchByAllLocations(u, searchedData) //pesquisa por tudo que eh localizacao
            },

            // Chamando os demais metodos que fazem a pesquisa pelos dados educacionais
            searchByAllLocations (u, searchedData)
            {
                return this.hasProvince(u, searchedData)
                    || this.hasCity(u, searchedData)
                    || this.hasDistrict(u, searchedData)
                    || this.hasAdminstrativePost(u, searchedData)
                    || this.hasMunicipalDistrict(u, searchedData)
                    || this.hasBairro(u, searchedData)
                    || this.hasLocality(u, searchedData)

            },

//          Verificacao de dados de Localizacao
            hasProvince (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.province
                    && userLocation.province !== ''
                    && (userLocation.province.toLowerCase().includes(search.toLowerCase()))
            },

            hasCity (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.city
                    && userLocation.city !== ''
                    && (userLocation.city.toLowerCase().includes(search.toLowerCase()))
            },

            hasDistrict (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.district
                    && userLocation.district !== ''
                    && (userLocation.district.toLowerCase().includes(search.toLowerCase()))
            },

            hasAdminstrativePost (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.post
                    && userLocation.post !== ''
                    && (userLocation.post.toLowerCase().includes(search.toLowerCase()))
            },

            hasMunicipalDistrict (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.municipalDistrict
                    && userLocation.municipalDistrict !== ''
                    && (userLocation.municipalDistrict.toLowerCase().includes(search.toLowerCase()))
            },

            hasBairro (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.bairro
                    && userLocation.bairro !== ''
                    && (userLocation.bairro.toLowerCase().includes(search.toLowerCase()))
            },

            hasLocality (user, search) {
                const userLocation = user.Locations
                return this.hasLocation(user)
                    && userLocation.locality
                    && userLocation.locality !== ''
                    && (userLocation.locality.toLowerCase().includes(search.toLowerCase()))
            },

//            ======================= Educacionais ========================
//          Verificacao de dados educacionais
//            ===============================================================
            hasInstitution (user, search) {
                const userEducation = user.Educations
                return this.hasEducation(user)
                    && userEducation.institution !== undefined
                    && userEducation.institution !== null
                    && (userEducation.institution.toLowerCase().includes(search.toLowerCase()))
            },
            hasSubjects (user) {
                const userEducation = user.Educations
                return this.hasEducation(user)
                    && userEducation.subjects
                    && userEducation.subjects.length > 0
            },

            hasSubject (user, search) {
                const userEducation = user.Educations
                return this.hasEducation(user)
                    && this.hasSubjects(user)
                    && this.hasSomeSubject(userEducation.subjects, search)
            },

            hasSomeSubject (subjects, search) {
                let res = null
                subjects.forEach((s) => {
                    if (s.name.toLowerCase().includes(search.toLowerCase()))
                    {
                        res = true
                        return
                    }
                    res = false
                })
                return res
            },

            hasFaculty (user, search) {
                const userEducation = user.Educations
                return this.hasEducation(user)
                    && userEducation.faculty !== undefined
                    && userEducation.faculty !== null
                    && (userEducation.faculty.toLowerCase().includes(search.toLowerCase()))
            },

            hasCourse (user, search) {
                const userEducation = user.Educations
                return this.hasEducation(user)
                    && userEducation.course !== undefined
                    && userEducation.course !== null
                    && (userEducation.course.toLowerCase().includes(search.toLowerCase()))
            },

            hasDepartment (user, search) {
                const userEducation = user.Educations
                return this.hasEducation(user)
                    && userEducation.department !== undefined
                    && userEducation.department !== null
                    && (userEducation.department.toLowerCase().includes(search.toLowerCase()))
            },

        }
    }
</script>
<style scoped>
    .explicador-bar-fixed {
        position: fixed;
        z-index: 1;
        left: 0;
        width: 100%;
        /*left: 0;*/
    }
</style>


