<template>
    <v-app id="inspire">

        <!--Drawer do lado esquerdo.-->
        <v-navigation-drawer
            fixed
            clipped
            app
            disable-route-watcher
            :temporary="temporaryDrawer"
            v-model="drawer"
        >
            <!--Menu Left-->
            <menu-left :temporary="temporaryDrawer"></menu-left>

        </v-navigation-drawer>

        <!--Drawer do lado direito.-->
        <v-navigation-drawer
            fixed
            clipped
            temporary
            right
            v-model="drawerRight"
        >
            <v-list dense>
                <template>

                    <menu-right></menu-right>

                </template>
            </v-list>
        </v-navigation-drawer>

        <!--Barra de cima-->
        <v-toolbar
                color="blue darken-3"
                dark
                app
                clipped-left
                clipped-right
                fixed
        >
            <v-toolbar-title :style="$vuetify.breakpoint.smAndUp ? 'width: 300px; min-width: 250px' : 'min-width: 72px'"  class="ml-0 pl-3">
                <!--Icon que esconde ou mostra o menu de pesquisa-->
                <v-toolbar-side-icon v-if="isHomePage" @click.stop="drawer = !drawer"></v-toolbar-side-icon>

                <!--Titulo para dispositivos maiores-->
                <span class="hidden-xs-only" style="cursor: pointer" @click="pushMainPage">
                    <v-icon v-if="!isHomePage">home</v-icon> {{ title }}
                </span>

                <!--Titulo para mobile devicess-->
                <span class="hidden-sm-and-up" style="cursor: pointer" @click="pushMainPage">
                    <v-icon v-if="!isHomePage" class="mb-1">home</v-icon> {{ title.substr(0,3) }}
                </span>

            </v-toolbar-title>

            <div style="max-width: 600px; min-width: 150px;">
                <!--Campo de pesquisa-->
                <div :style="{width: textFieldWidth, 'margin-bottom': !showSearchResult || $vuetify.breakpoint.smAndDown ? 0 : '-20px'}"
                     style="max-width: 600px; top: 0; bottom: auto;" >
                    <v-text-field
                            v-if="showSearch || !temporaryDrawer"
                            light
                            solo
                            prepend-icon="search"
                            v-model="search"
                            placeholder="Pesquise pela Provincia, Cursos, Universidade, Disciplinas, Bairros . . ."
                            class="ml-100px"
                            style="max-width: 100%;"
                            @click="showSearchResult = true"
                    ></v-text-field>

                    <!--Indicador das opcoes de filtro-->
                    <div v-if="showFilterResult && (filterType && filterItem) && usersListResult.length" class="search-panel elevation-1 white">
                        <h3>Encontrados ({{ usersListResult.length }}) explicadores de</h3>
                        <h4><b>{{ filterType }}</b>  | {{ filterItem }}</h4>
                        <a @click="onCloseFilter">Limpar Filtro</a> | <a @click="showFilterResult = false">Fechar</a>
                    </div>

                    <!--Div com resulsutados de filtro-->
                    <div v-if=" showSearchResult"
                         class="white ml-100px input-group elevation-2 pa-0 hidden-xs-only"
                         style="height: 20px; position: relative; margin-top: 1px;">

                        <v-container
                            class='white text-xs-center elevation-2'
                            v-if="usersListResult.length === 0" style="height: 40px">
                            <v-layout wrap class='red--text text-xs-center'>
                                <p>Sem ocorrencias</p>
                            </v-layout>
                        </v-container>
                        <v-container v-else="" class='pa-0'>

                            <v-layout>
                                <v-flex xs12 ofsset-xs8 v-if="!showFilterResult && filterType && filterItem">
                                    <a @click="showFilterResult = !showFilterResult" style="float: right; margin: 0 -70%">Mostrar Indicador</a>
                                </v-flex>
                                <v-flex xs12 ofsset-xs8>
                                    <a @click="onCloseFilter" style="float: right; margin: 0 -70%">Lmpar Filtros</a>
                                </v-flex>
                                <v-flex xs12>
                                    <a @click="showSearchResult = !showSearchResult" style="display: flex;flex-direction: row-reverse; margin: 0 2px;">Fechar</a>
                                </v-flex>
                            </v-layout>
                            <v-layout wrap class='ma-0 azul-principal elevation-2'>
                                <search-result keyType='Províncias' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Distritos' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Cidades' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Dist. Municipais' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Postos Administrativos' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Bairros' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Universidades' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Escolas' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Institutos' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Faculdades' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Departamentos' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Cursos' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Classes' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Disciplinas' :usersListResult="usersListResult"></search-result>
                                <search-result keyType='Outros' :usersListResult="usersListResult"></search-result>
                            </v-layout>
                        </v-container>
                    </div>

                </div>

            </div>

            <!-- Limpa o campo de pesquisa nos dispositivos maiores-->
            <v-btn v-if="search !== '' && !temporaryDrawer" icon title="Limpar pesquisa" @click="search = ''"><v-icon>close</v-icon></v-btn>

            <div class="d-flex align-center" style="margin-left: auto">

                <!-- Limpa o campo de pesquisa nos dispositivos menores-->
                <v-btn v-if="search !== '' && temporaryDrawer" icon title="Limpar pesquisa" class="primary" @click="search = ''">
                    <v-icon>close</v-icon>
                </v-btn>

                <!--Fecha o campo de pesquisa nos dispositivos menores-->
                <v-btn v-if="search === '' && temporaryDrawer && showSearch" icon title="Fechar o campo" @click="showSearch = !showSearch"><v-icon>close</v-icon></v-btn>

                <!--Icone de pesquisa que so aparece no mobile-->
                <v-btn v-if="!showSearch && route === 'Home' && temporaryDrawer" icon @click="showSearch = !showSearch">
                    <v-icon>search</v-icon>
                </v-btn>

                <!--Opcao do user nao autenticado, vem do componente dropdown-->
                <auth-dropdown></auth-dropdown>

                <!--Menu dropdown para as outras funcionalidades da pagina-->
                <v-menu v-if="false" offset-y xs3>
                    <v-btn icon class="hidden-xs-only" slot="activator">
                        <v-icon>apps</v-icon>
                    </v-btn>
                    <v-list>
                        <div>
                            <v-list-tile router to="#">
                                <v-list-tile-action>
                                    <v-icon>info</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-title>Centros de Explicacao</v-list-tile-title>
                            </v-list-tile>

                            <v-list-tile router to="#">
                                <v-list-tile-action>
                                    <v-icon>info</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-title>Bibliotecas </v-list-tile-title>
                            </v-list-tile>

                            <v-list-tile router to="#">
                                <v-list-tile-action>
                                    <v-icon>info</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-title>Material </v-list-tile-title>
                            </v-list-tile>

                        </div>
                    </v-list>
                </v-menu>

                <!--Icone de notificacoes que so aparece quando houver novas notificacoes-->
                <!--Notificacoes-->
                <!--<v-btn icon class="hidden-xs-only">-->
                    <!--<v-icon>notifications</v-icon>-->
                <!--</v-btn>-->


                <v-tooltip bottom>
                    <v-btn icon  slot="activator" class="hidden-xs-only" v-if="isHomePage" @click.stop="drawerRight = !drawerRight">
                        <v-icon>bubble_chart</v-icon>
                    </v-btn>
                    <span>Explicadores populares</span>
                </v-tooltip>
            </div>
        </v-toolbar>

        <!--Conteudo central-->
        <v-content>

            <!-- O conteudo central sera renderizado aqui -->
            <router-view/>

            <!-- colocando o progressbar que aparece em cima-->
            <vue-progress-bar></vue-progress-bar>

        </v-content>

        <!--Montar aqui a dialog de registo-->
        <template>

            <!--Dialog com as mensagens de confirmacao-->
            <confirm-dialog></confirm-dialog>

            <login-dialog v-if="isHomePage && !isUserAuth"></login-dialog>
            <home-fab-buttom v-if="!isHomePage"></home-fab-buttom>

        </template>

        <!--Rodape da pagina-->
        <v-footer :fixed="fixed" app>
            <span>&copy; Explicador 2018</span>
        </v-footer>

        <!--Snackbar para as notificacoe em baixo-->
        <v-snackbar
            :timeout="snackbarTimeout"
            :color="snackBarColor"
            bottom
            v-model="snackbar"
        >
            {{ snackBarText }}
            <v-btn flat color="pink" @click.prevent="snackbar = false">Fechar</v-btn>
        </v-snackbar>

    </v-app>
</template>

<script>
    import { eventBus } from './main'
    export default {
        name: 'App',
        data: () => ({
            title: 'Explicador',
            search: '',
            showSearch: !this.temporaryDrawer,
            filterType: '',
            filterItem: '',
            showSearchResult: false,
            showFilterResult: true,
            usersListResult: [],
            drawer: this.route === 'Home' ? this.temporaryDrawer : false, //controlando drawer, para mostrar apenas se estiver na pagina principal
            drawerRight: false,
            fixed: true,
            temporary: false,
            snackbar: false,
            snackbarTimeout: 6000,
            snackBarText: '',
            snackBarColor: 'green'

        }),
        beforeCreate () {

            let vm = this
//            Busca os distritos depois de 3 segundos
            setTimeout(function(){ vm.$store.dispatch('getAllDistricts') }, 3000);

        },
        mounted () {

//            Escuta de evento que fecha os drawers esquerdo ou direito
            eventBus.$on('closeDrawer', (position) => {

                if (position === 'left'){
                    this.drawer = !this.drawer
                }
                if (position === 'right'){
                    this.drawerRight = !this.drawerRight
                }

            })
            eventBus.$on('ocultarMenus',  (value) => {

                this.drawer = value
                this.drawerRight = value

            })

            eventBus.$on('advancedSearchResult',  (value) => {

                this.usersListResult = value

            })

            eventBus.$on('showWhatIsFiltering', (filterType, filterItem) => {
                this.showFilterResult =  true
                this.filterType = filterType
                this.filterItem = filterItem
            })

            eventBus.$on('showSnackbar', (text, color, timeout) => {

                this.snackBarColor = color
                this.snackBarText = text
                this.snackbarTimeout = timeout
                this.snackbar = true

            })
            eventBus.$on('semNet', () => {

                this.semNet()

            })

        },
        computed: {
            isSideMenusShowing () {
                return this.$store.getters.isSideMenusShowing
            },
            isHomePage () {
                return this.route === 'Home'
            },
            users () {
                return this.$store.getters.getUsers
            },
            areUsers () {
                return this.users.length > 0
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isLoginPage () {
                return this.$store.getters.isLoginPage
            },
            isRegisterPage () {
                return this.$store.getters.isRegisterPage
            },
            route () {
                return this.$route.name
            },
            textFieldWidth () {

                if (this.temporaryDrawer) return '100%'

                return '150%'

            },
            temporaryDrawer () {
//                console.log(this.$vuetify.breakpoint.width)
                switch (this.$vuetify.breakpoint.name) {
                    case 'xs':
                    case 'sm': return true
                    case 'md':
                    case 'lg':
                    case 'xl': return false
                }
            }
        },
        watch: {
            // verificando a rota actual, para que ao sair do perfil, a variavel que controla o user que se esta a visualizar o seu perfil, fique nula.
            route (value) {

                this.$store.dispatch('setProfileUserNull')
                this.$store.dispatch('clearError')

                this.drawer = false
                this.search = ''
                this.showSearch = ''

                if (value !== 'Home' && this.drawer)
                {
                    this.drawer = false

                } else if (value === 'Home' && !this.temporaryDrawer) {

                    this.drawer = true

                }


            },
            // value = true, significa que eh temporario, oculta o menu para os dispositivos menores
            // value = false, significa ficar sempre visivel, mostrar o drawer nos dispositivos maiores como laptop
            temporaryDrawer (value)
            {

                this.showSearch = !value
                this.drawer = !value //Se o drawer for temporario, vamos esconder. Caso o user quiser visualizar vai clicar no mesmo.
                if (this.route !== 'Home')
                {
                    this.drawer = false
                }
            },
//          Pesquisa implementada aqui. Quando se pesquisa, um evento eh emitido e processado no ficheiro Home.
//          veja o evento $on('searchForExplicador) No ficheiro home.//
            search (value) {

                eventBus.$emit ('searchForExplicador', value)
                if (value === '')
                {
                    this.onCloseFilter()
                } else {

                    this.showSearchResult = true

                }

            }
        },
        methods: {

            pushMainPage () {
                // Reseta os criterios de pesquisa de dados na pagina, para o default
                // Este evento eh tratado nos metodos created () dos Componentes Home.vue e MainMenuLeft.vue
                this.onCloseFilter()
                this.$router.push('/')
            },
            onCloseFilter () {
                // Reseta os criterios de pesquisa de dados na pagina, para o default
                // Este evento eh tratado nos metodos created () dos Componentes Home.vue e MainMenuLeft.vue
                this.search = ''
                this.filterType = ''
                this.filterItem = ''
                this.showFilterResult =  false
                eventBus.$emit('resetExplicadoresFilter');
            },
            semNet () {

                let title = 'Sem conexão a internet'
                let message = 'Voçê não tem acesso a internet, verifique a sua conexão ou clique a tecla F5 para refrescar a pagina, .. .\n' +
                              'Se tiver acesso a internet, pode sair da sua conta e entrar novamente! '
                const btn1 = 'Fechar'
                const btn2 = 'Ok'

//              Chamando a tela de confirmacao global, para informar o user que ele nao esta autenticado
                /**
                 * Primeiro param: true: significa abrir a dialog
                 * Segundo param:  title: o titulo a mostrar
                 * Terceiro param:  msg: a mensagem a mostrar
                 */
                this.$store.dispatch('togleConfirmDialog', {show: true, title: title, message: message,btn1: btn1,btn2: btn2})
            }

        }
    }
</script>

<style>

    .search-result {
        max-width: 530px;
    }
    .search-panel {
        /*background: red;*/
        color: black;
        /*height: 5px;*/
        border-radius: 5px;
        /*margin-left: 838px;*/
        right: 0px;
        margin: 0 5px;
        padding: 15px;
        position: absolute;
    }
    @media screen and (max-width: 1175px){
        .search-result {
            max-width: 380px;
        }
    }
    @media screen and (max-width: 958px){
        .search-result {
            max-width: 256px;
        }
    }
    @media screen and (max-width:800px){
        .search-result {
            max-width: 30%;
        }
    }
    @media screen and (max-width: 600px){
        .search-result {
            max-width: 50%;
        }
    }
    .explicador-bar-fixed {
        position: fixed;
        z-index: 1;
        left: 0;
        width: 100%;
        /*left: 0;*/
    }
    .cabecalho-profile {
        margin: 6px;
        padding: 0px 10px 20px;
        font-weight: bold;
        font-size: 14pt;
        border-bottom: 1px solid blue;
        color: blue
    }
    .azul-principal {
        background: #1565C0!important;
    }
    .azul-principal--text {
        color: #1565C0!important;
    }
    .img-radius {
        display: block;
        margin-left: auto;
        margin-right: auto;
        border-radius: 100%;
        width: 90px;
        height: 90px!important;
    }

    /* Estilo para adicionar pading nos botoes de add que chamam as dialogs no perfil*/
    @media screen and (min-width: 1180px) {
        .ml-100px {
            margin-left: 100px
        }
    }

    @media screen and (max-width: 900px) {
        .personalized-pr {
            padding-right: 5px !important;
        }
    }

    @media screen and (max-width: 400px) {
        .personalized-pr {
            padding-right: 35px!important;
        }
    }

    /*Estilo para o icon de processamento nos botoes de registo*/
    .custom-loader {
        animation: loader 1s infinite;
        display: flex;
    }
    @-moz-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    @-webkit-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    @-o-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    @keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    .firebase-docs-color {
        color: #039be5;
    }
    .icon {
        font-size: 20px;
    }
</style>


