import * as firebase from 'firebase'
import router from '../../router/index'

export default {
    state: {
        userAuth: null,
        user: null,
        db_users: [],
        auth_img: 'https://avatars0.githubusercontent.com/u/18400142?s=460&v=4',
        auth_img1: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTVzJOZxVrO-feT6NaYjyfyhRrtzVyhPVLq39tnTqhG1uXxDuelvg',
        auth_img2: 'https://partners.starcertification.org/Content/Partner_Guest/images/graduation_boy-512.png',
        isConfirmMailSent: false,
    },

    mutations: {

        setUserAuth (state, user) {

            state.userAuth = user //atribui o user autenticado para ser guardado

            if ( user !== null && user !== undefined) { //Verifica se foi enviado algum user ou nao

                let us = state.db_users.find( user => { // Buscando o user na lista dos user recuperados da base de dados.

                    return user.id === state.userAuth.id

                })

                if (us !== undefined) { //Verificamos se o user foi encontrado na lista ou nao

                    state.userAuth = us

                }

            }

        },

        setUser(state, user) {
            state.user = user
        },

        setConfirmMailSent(state, value) {
            state.isConfirmMailSent = value
        },

        setDBUser (state, users) {
            state.db_users = users
        },


    },

    getters: {
        userAuth (state) {
            return state.userAuth
        },
        user (state) {
            return state.user
        },

        getConfirmMailSent (state) {
            return state.isConfirmMailSent
        },

        getAuthProvider (state) {

            let user = state.userAuth

            if (user === null || user === undefined)  return null //se o user nao estiver autenticado retorna null

            if (user.provider === 'google.com') return 'google' //retorna apenas google se o provider for google.com
            if (user.provider === 'facebook.com') return 'facebook'  //retorna apenas facebook se o provider for facebook.com

            return user.provider

        },

        getUsers (state) {

            if (state.db_users.length  <= 0) return []

            const users = []
            state.db_users.forEach((u) => {

                if (!u.recomended)
                {
                    users.push(u)
                }

            })
            return users

        },

        getFakulUsers (state) {

            if (state.db_users.length  <= 0) return []

            const users = []
            state.db_users.forEach((u) => {

                if (u.recomended)
                {
                    users.push(u)
                }

            })
            return users

        },

    },

    actions: {

        setProfileUser ({commit, state}, id) {
            commit('setLoading', true)

            let user = state.db_users.find(u => {
                return u.id === id
            })

            if (user !== null && user !== undefined )
            {
                commit('setUser', user)
            }

            commit('setLoading', false)
        },

        //Se houver o erro de por padrao o explicador possuir a minha imagem
        //Chamado para actualizar globalmente a imagem padrao de perfil dos utilizadores que ainda nao alteraram a padrao
        // So de chamar este metodo ele define a imagem no url do auth_img1, qualquer outra nova eh so por aqui.

        setProfilePhoto ({commit, state}) {

            state.db_users.forEach(u => {

                if (u.photoURL === state.auth_img && !u.emailVerified)
                {
                    u.photoURL = state.auth_img1

                    const db = firebase.firestore()
                    db.collection("Users").doc(u.id).set(u)
                        .then(() => {
                        })
                        .catch((error) => {
                            console.log('Ocorreu um erro ao gravar user actualizado no firestore: ', error)
                        })

                }

            })

        },

        setProfileUserNull ({commit, state}) {
            if (state.user !== null && state.user !== undefined)
                commit('setUser', null)
        },

        createUser ({commit,dispatch}, value) {

            commit('setLoading',true)
            commit('clearError')

            firebase.auth().createUserWithEmailAndPassword(value.email, value.password)
                .then((fbUser) => {
                        return fbUser
                    }
                )
                .then((fbUser) => {

                    fbUser.recomended = value.recomended

                    if (value.recomended) {

                        dispatch('saveOnlineUserData', fbUser)

                    } else {

                        firebase.auth().currentUser.sendEmailVerification() //enviando email de verificacao
                            .then(() => {

                                dispatch('saveOnlineUserData', fbUser)

                            })
                            .catch((error) => {
                                commit('setLoading', false)
                                commit('setError', error)
                                console.log(error)
                            })
                    }

                })
                .catch(
                    error => {
                        commit('setLoading',false)
                        commit('setError',error)
                        console.log(error)
                    }
                )

        },

        saveOnlineUserData ({commit, state}, userCreated) {

            userCreated = firebase.auth().currentUser

            // Procurando o user para ver se ja foi gravado previamente
            let usReg = state.db_users.find( user => {
                return user.id === userCreated.uid
            })

            // Guardamos na sessao logo aqui  pois o user ja foi registado previamente
            if (usReg !== null && usReg !== undefined) {
                commit('setUserAuth', { id: userCreated.uid })
                commit('setLoading', false)
                router.push('/' + userCreated.uid + '/profile')
                return
            }

            const db = firebase.firestore() //instancia da base de dados

            const user = {
                id: userCreated.uid,
                name: userCreated.displayName,
                displayName: userCreated.displayName,
                email: userCreated.email,
                gender: '',
                bio: '',
                telfone: '',
                recomended: userCreated.recomended,
                photoURL: userCreated.photoURL ? userCreated.photoURL : state.auth_img1 ,
                emailVerified: userCreated.emailVerified,
                provider: userCreated.providerData[0].providerId,
                Educations: {
                    institution: '',
                    course: '',
                    faculty: '',
                    department: '',
                    subjects: []
                }
            }

            db.collection("Users").doc(userCreated.uid).set(user)
                .then((userSaved) => {

                    firebase.auth().currentUser.updateProfile(user) //nao testaddo ainda

                    // Guardar este user na sessao de userAuth
                    state.db_users.push(user) //Gravando o novo user na lista
                    commit('setUserAuth', user)
                    commit('setLoading', false)
                    commit('setConfirmMailSent', true)
                    router.push('/' + userCreated.uid + '/profile')

                })
                .catch((error) => {
                    console.log('Ocorreu um erro ao gravar user no firestore: ', error)
                    commit('setLoading', false)
                    commit('setError', error)
                })

        },

        updateOnlineUserData ({commit, state, dispatch}, value) {

            commit('setLoading', true)
            commit('clearError')

            state.userAuth = value //Modificando o user autenticado aqui.


            const db = firebase.firestore()
            db.collection("Users").doc(state.userAuth.id).set(value)
                .then(() => {

                    firebase.auth().currentUser.updateProfile(value) //nao testaddo ainda

                    // Guardar este user na sessao de userAuth
                    dispatch('getAllUsers')

                    commit('setLoading', false)


                })
                .catch((error) => {
                    console.log('Ocorreu um erro ao gravar user no firestore: ', error)
                    commit('setLoading', false)
                    commit('setError', error)
                })

        },

        uploadPhoto ({commit, state, dispatch}, value) {

            commit('clearError')
            commit('setLoading', true)

            const filename = value.image.name
            const ext = filename.slice(filename.lastIndexOf('.'))

            dispatch('finalizePhotoUpload', value)

            // NOTA: deletar primeiro a imagem anteror
            // Deletando primeiro o ficheiro a ser actualizado
            // firebase.storage().ref('ProfilePhotos/' + state.userAuth.id + '.' + ext).delete()
            //     .then(() => {
            //         dispatch('finalizePhotoUpload', value)
                // })
                // .catch((error => {
                //     dispatch('finalizePhotoUpload', value)
                // }))


        },

        finalizePhotoUpload ({commit, state, dispatch}, value) {

            const db = firebase.firestore()

            const filename = value.image.name
            const ext = filename.slice(filename.lastIndexOf('.'))

            // Actualizando a nova imagem...
            firebase.storage().ref('ProfilePhotos/' + state.userAuth.id + '.' + ext).put(value.image) //gravando a imagem com o nome como id da sugestao
                .then(fileData => {

                    if (fileData === null || fileData === undefined) return

                    let imageUrl = fileData.metadata.downloadURLs[0]
                    return db.collection("Users").doc(state.userAuth.id).update({photoURL: imageUrl})

                })
                .then(() => {

                    // Guardar este user na sessao de userAuth
                    dispatch('getAllUsers')

                    commit('setLoading', false)

                })
                .catch((error) => {
                    console.log(error)
                    commit('setLoading',false)
                    commit('setError', error)
                })



        },

        googleSignIn ({commit, dispatch}) {

            const provider = new firebase.auth.GoogleAuthProvider();

            firebase.auth().languageCode = 'pt'; //especificando o idioma a ser usado

            firebase.auth().signInWithPopup(provider)
                .then((user) => {

                    dispatch('saveOnlineUserData', user)

                })
                .catch((error) => {
                    commit('setError', error)
                    console.log(error)
                })

        },

        facebookSignIn ({commit, dispatch}) {

            const provider = new firebase.auth.FacebookAuthProvider();

            firebase.auth().languageCode = 'pt'; //especificando o idioma a ser usado

            firebase.auth().signInWithPopup(provider)
                .then((user) => {

                    dispatch('saveOnlineUserData', user)

                })
                .catch((error) => {
                    commit('setError', error)
                    console.log(error)
                })

        },

        loginUser ({commit},  value,) {

            commit('setLoading',true)
            commit('clearError')

            firebase.auth().signInWithEmailAndPassword(value.email, value.password)
                .then(
                    user => {
                        const newUser = {
                            id: user.uid,
                        }
                        commit('setUserAuth', newUser)
                        commit('setLoading', false)
                        router.push('/' + user.uid + '/profile')
                    }
                )
                .catch(
                    error => {
                        commit('setLoading', false)
                        commit('setError', error)
                        console.log(error)
                    }
                )

        },

        saveDeletedUser ({commit, state, dispatch}) {

            const user = firebase.auth().currentUser

            // Procurando o user para ver se ja foi gravado previamente
            let usReg = state.db_users.find( u => {
                return u.id === user.uid
            })

            const db = firebase.firestore() //instancia da base de dados

            db.collection("UsersDeleted").add(usReg)
                .then((userSaved) => {
                    return userSaved
                })
                .then(user => {

                    //Deletar da nossa lista de user na db
                    dispatch('deleteUserFromUsers')

                })
                .catch((error) => {
                    console.log('Ocorreu um erro ao gravar o user deletado no firestore: ', error)
                    commit('setLoading', false)
                    commit('setError', error)
                })

        },

        deleteUserFromUsers ({commit, state, dispatch }) {

            // Esse user nao temos na nossa bd podemos remover, nao executamos mais
            if (state.userAuth === null || state.userAuth === undefined)  return //

            const db = firebase.firestore() //instancia da base de dados

            db.collection("Users").doc(state.userAuth.id).delete()
                .then(() => {

                    //Deletar o user no firebase auth
                    firebase.auth().currentUser.delete()

                    //Saindo da sessao
                    dispatch('logOutUser')

                    //Encerrando o processamento
                    commit('setLoading', false)

                    router.push('/')
                })
                .catch((error) => {
                    console.log('Ocorreu um erro ao deletar o user do firestore: ', error)
                    commit('setLoading', false)
                    commit('setError', error)
                })

        },

        deleteAuthUserAccount ({commit, dispatch}, value) {

            commit('setLoading',true)
            commit('clearError')

            firebase.auth().signInWithEmailAndPassword(value.email, value.password)
                .then(
                    user => {

                        // let firebase.
                        //gravar no firebase o user a ser removido
                        dispatch('saveDeletedUser')

                        //Indo para a pagina principal
                        // router.push('/')
                    }
                )
                .catch(
                    error => {
                        commit('setLoading', false)
                        commit('setError', error)
                        console.log(error)
                    }
                )

        },

        sendUserPasswordResetEmail ({commit},  value,) {

            if (value === null || value === undefined) return

            commit('setLoading',true)
            commit('clearError')

            firebase.auth().sendPasswordResetEmail(value.email)
                .then(() => {
                    //concluido com sucesso
                    commit('setLoading', false)
                })
                .catch((error) => {
                    commit('setLoading', false)
                    commit('setError', error)
                    console.log(error)
                })
        },

        updateUserPassword ({commit},  value,) {

            commit('setLoading',true)
            commit('clearError')

            firebase.auth().signInWithEmailAndPassword(value.email, value.previousPassword)
                .then(
                    user => {
                        user.updatePassword(value.password)
                            .then((resp) => {

                                commit('setLoading', false)

                            })
                            .catch((err) => {
                                commit('setLoading', false)
                                commit('setError', error)
                                console.log(error)
                            })
                    }
                )
                .catch(
                    error => {
                        commit('setLoading', false)
                        commit('setError', error)
                        console.log(error)
                    }
                )

        },

        sendVerificationLink({commit}) {

            commit('setLoading',true)
            commit('clearError')

            firebase.auth().currentUser.sendEmailVerification() //enviando email de verificacao
                .then(() => {
                    commit('setLoading',false)
                    console.log('email de verificacao enviada com sucesso!')
                })
                .catch((error) => {
                    commit('setLoading', false)
                    commit('setError', error)
                    console.log(error)
                })

        },

        logOutUser ({commit}) {
            firebase.auth().signOut()
            commit('setUserAuth', null)
            router.push('/')
        },

        autoSignIn ({commit,state, dispatch}, user) {

            const userCached = {
                id: user.uid,
                name: user.displayName,
                email: user.email,
                gender: '',
                telefone: '',
                photoURL: user.photoURL && user.photoURL !== null ? user.photoURL : state.auth_img2 ,
                emailVerified: user.emailVerified,
                provider: user.providerData[0].providerId,
            }

            commit('setUserAuth', userCached)

        },

        //Esta actualiza os dados basicos do utilizadores autenticado, dados como:
        // 1. O estado da confirmacao
        // 2. A photo de perfil : Este troca a minha foto pessoal quue eu havia colocado como teste.
        updateIfHasConfirmed ({commit, state, dispatch}, user) {

            let us = state.db_users.find( u => { // Buscando o user na lista dos user recuperados da base de dados.

                return u.id === user.id

            })

            let hasConfirmationChanged = us !== undefined && us.emailVerified !== user.emailVerified
            let isPhotoPrevious = us !== undefined && us.photoURL === state.auth_img && !us.emailVerified && us.provider === 'Password'

            if (hasConfirmationChanged || isPhotoPrevious) { //Verificamos se o user foi encontrado na lista ou nao

                us.emailVerified = user.emailVerified
                us.photoURL = state.auth_img1
                dispatch('updateOnlineUserData', us)

            }

            commit('setUserAuth', user)

        },

        // Busca a lista dos users e depois actualiza o estado do user autenticado
        getAllUsers ({commit, state, dispatch}) {

            const db = firebase.firestore()
            db.collection("Users").get().then(function(querySnapshot) {

                const userss = []

                querySnapshot.forEach(function(doc) {

                    const user = doc.data()

                    userss.push( user )

                });

                commit('setDBUser',userss)

                return userss

            }).then( list => {

                // Verifica se o user esta autenticado, se nao estiver cancele a busca
                if (state.userAuth === null || state.userAuth === undefined) return

                dispatch('updateIfHasConfirmed', state.userAuth) //chamando o metodo para verificar se ele conifrmou o emil recentemente
                commit('setLoading', false)

            })
            .catch( (error => {
                console.log('erro buscando a lista dos users', error)
            }))


        },

        confirmationMailSet ({commit}, value) {

            commit('setConfirmMailSent', value)

        },

    }
}


